@import "colors";
@import "mixins";

* {
  box-sizing: border-box;
  letter-spacing: 0.04em; }
.g-container {
  width: 980px;
  margin: 0 auto;
  font-size: 14px;
  position: relative;
  background: white;
  @include bp-table {
    width: 100%; } }

.g-title-special {
    font-size: 0.9em;
    font-family: "pfdisplay";
    color: white;
    font-weight: bold;
    font-style: italic;
    line-height: 1.2;
    position: absolute;
    right: 15px;
    top: 0;
    z-index: 99; }

.g-block {
  position: relative;
  padding: 0 55px;
  overflow: hidden;
  @include bp-table-portrait {
    height: 100vh !important; }
  &__container {
    display: flex;
    @include bp-table {
      flex-direction: column; }
    > section {
      flex: 1;
      padding: 0;
      height: 420px;
      display: flex;
      .g-title {
        position: relative;
        @include bp-table {
          height: 305px; } }
      .g-button {
        position: absolute; }
      @include bp-table {
        height: 800px;
        background-size: cover; } } }


  @include bp-table {
    padding: 0;
    justify-content: center; } }


.g-title {
  position: relative;
  z-index: 99;
  h3 {
    font-size: 3em;
    margin: 0;
    font-family: "yeseva", cursive;
    @include bp-table {
      font-size: 9.5vw; } }
  span {
    white-space: nowrap;
    &:last-child {
      white-space: nowrap;
      font-family: "pfdisplay"; } }
  p {
    margin: 0;
    font-size: 1.2em;
    font-family: "opensans", sans-serif;
    font-weight: bold;
    @include bp-table {
      font-size: 4vw; } }
  &-p {
    @include bp-table {
      padding: 0 10%; }
    h3 {
      line-height: 0.6;
      padding-bottom: 20px;
      @include bp-table {
        padding-bottom: 6vw; } }
    span:last-child {
      color: $title; }
    p {
      text-align: center;
      &:nth-child(2) {
        border-top: 2px dotted grey;
        padding-top: 15px; } }

    .g-button {
      margin: 40px auto 75px; } }
  &-white {
    color: white;
    background: rgba(black, 0.3);
    width: 100%;
    h3 {
      line-height: 1; } } }

.g-button {
  display: flex;
  align-items: center;
  justify-content: center;
  @include linear-gradient(90deg, $btn, $btn2);
  border-radius: 50%;
  width: 65px;
  height: 65px;
  text-align: center;
  outline: none;
  border: 0;
  box-shadow: 2px 2px 3px 1px rgba(0,0,0,0.4);
  cursor: pointer;
  @include transition(box-shadow .25s ease-in-out);
  @include bp-table {
    width: 80px;
    height: 80px; }
  &-txt {
    width: 200px;
    height: 60px;
    padding: 20px 25px;
    justify-content: flex-end;
    border-radius: 30px;
    @include bp-table {
      height: auto; } }
  &:hover {
    box-shadow: 2px 2px 8px 1px rgba(0,0,0,0.6); }
  span {
    font-size: 1.2em;
    font-family: "opensans", sans-serif;
    color: white;
    font-weight: bold;
    line-height: 1;
    text-transform: uppercase;
    padding-right: 10px;
    @include bp-table {
      font-size: 1.3em; } } }






.g-first {
  background: url('../assets/img/block1a.png') no-repeat;
  background-position: right;
  background-size: contain;
  height: 490px;
  display: flex;
  align-items: center;
  @include bp-table {
    background: url('../assets/img/block1a-mob.png') no-repeat;
    background-position: top;
    background-size: cover;
    height: 800px;
    color: white; }
  h3 {
    font-size: 4em;
    @include bp-table {
      font-size: 12vw; } }
  .g-title {
    span:last-child {
      margin-left: 32%;
      @include bp-table {
        margin-left: 36vw;
        color: white; } } }
  .g-button {
    min-width: 215px;
    @include bp-table {
      min-width: auto; } }
  .g-product {
    position: absolute;
    width: 310px;
    bottom: -1px;
    right: 38%;
    @include bp-table {
      right: 0; } } }

.g-second {
  background: url('../assets/img/block2.png') no-repeat;
  background-position: top;
  background-size: cover;
  padding: 0 0 0 55px;
  align-items: flex-end;
  .g-title {
    padding: 30px 15px;
    @include bp-table {
      padding: 60px 15px; }
    @include transition(padding .25s ease-in-out);
    span:last-child {
      margin-left: 35px;
      @include bp-table {
        margin-left: 7vw; } } }
  &:hover {
    .g-title {
      padding: 95px 15px 30px 15px;
      @include bp-table {
        padding: 60px 15px; } }
    .g-button {
      box-shadow: 2px 2px 8px 1px rgba(0,0,0,0.6); } }
  .g-button {
    bottom: 152px;
    left: 50%;
    transform: translateX(-50%);
    @include bp-table {
      bottom: 265px; } } }


.g-third {
  background: url('../assets/img/block3.png') no-repeat;
  background-position: top;
  background-size: 100%;
  padding: 0 55px 0 0;
  align-items: flex-start;
  @include transition(background-size .5s ease-in-out);
  &:hover {
    background-size: 110%;
    .g-button {
      box-shadow: 2px 2px 8px 1px rgba(0,0,0,0.6); }
    @include bp-table {
      background-size: cover; } }
  .g-title {
    padding: 25px 15px 45px;
    @include bp-table {
      padding: 60px 15px 80px; }
    span:last-child {
      margin-left: 55px;
      @include bp-table {
        margin-left: 9vw; } } }
  .g-button {
    bottom: -30px;
    left: 50%;
    transform: translateX(-50%);
    @include bp-table {
      bottom: -38px; } } }


.g-fourth {
  height: 410px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  @include bp-table {
    height: 700px; }
  .g-title {
    text-align: center;
    h3 {
      padding: 20px 0;
      line-height: .8;
      span:first-child {
        margin-right: 110px;
        @include bp-table {
          margin-right: 0; } }
      span:last-child {
        margin-left: 120px;
        @include bp-table {
          margin-left: 3vw; } } } }
  .g-decor {
    position: absolute;
    width: 405px;
    top: 0;
    left: 70px;
    @include bp-table {
      left: 1vw;
      width: 600px; } }
  .g-product {
    position: absolute;
    width: 365px;
    bottom: -1px;
    right: 30px;
    @include bp-table {
      right: 0; } }
  .g-button {
    margin: 30px auto 0; } }


.g-fifth {
  background: url('../assets/img/block5.png') no-repeat;
  background-size: cover;
  height: 420px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  @include bp-table {
    background: url('../assets/img/block5-mob.png') no-repeat;
    background-size: cover;
    height: 1000px;
    align-items: flex-end;
    justify-content: center;
    overflow: visible;
    .g-title {
      background: $grey;
      width: 100%;
      padding: 30px 10% 60px; }
    .g-button {
      position: absolute;
      bottom: -38px;
      left: 50%;
      transform: translateX(-50%); } }
  .g-button {
    margin: 30px auto 0; }
  h3 {
    padding: 25px 0;
    span:first-child {
      margin-left: 10px;
      @include bp-table {
        margin-left: 10vw; } }
    span:last-child {
      margin-left: 120px;
      @include bp-table {
        margin-left: 30vw; } } } }

.g-sixth {
  height: 410px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  @include bp-table {
    height: 700px; }
  .g-title {
    text-align: center;
    h3 {
      padding: 20px 0;
      line-height: .8;
      span:first-child {
        margin-right: 110px;
        @include bp-table {
          margin-right: 0; } }
      span:last-child {
        margin-left: 120px;
        @include bp-table {
          margin-left: 3vw; } } } }
  .g-product {
    position: absolute;
    &:first-child {
      top: 0;
      left: 85px;
      @include bp-table {
        left: 0; } }
    &:last-child {
      bottom: 0;
      right: 35px;
      @include bp-table {
        right: -80px; } } }
  .g-button {
    margin: 30px auto 0; } }

.g-seventh {
  background: url('../assets/img/block7.png') no-repeat;
  background-size: cover;
  height: 420px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 20px;
  @include bp-table-600 {
    background-position: top right !important; }
  @include bp-table {
    background: url('../assets/img/block7-mob.png') no-repeat;
    background-size: cover;
    background-position: top center;
    height: 800px;
    margin-bottom: 38vw;
    align-items: flex-end;
    justify-content: center;
    overflow: visible;
    .g-title {
      position: absolute;
      bottom: -35vw;
      padding: 0;
      left: 3%; } }
  .g-title {
    @include bp-table {
      padding-bottom: 30px;
      display: flex;
      flex-direction: column-reverse;
      align-items: center;
      justify-content: center; }
    h3 {
      padding: 125px 0 0;
      line-height: .8;
      br {
        display: none; }
      @include bp-table {
        padding: 0;
        font-size: 8vw;
        line-height: 1;
        br {
          display: block; }
        span:nth-child(3) {
          margin-left: 25vw; } } }
    span:last-child {
      margin-left: 125px;
      @include bp-table {
        margin-left: 0; } } }
  .g-button {
    position: absolute;
    top: 55px;
    right: 250px;
    margin: 0;
    @include bp-table {
      position: static; } } }


.g-footer {
  background: $grey;
  padding: 30px 0 45px;
  &__title {
    font-size: 1.2em;
    font-family: "opensans", sans-serif;
    color: $darkGrey;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    @include bp-table {
      font-size: 4vw; } }
  &__social {
    display: flex;
    align-items: center;
    justify-content: center; }
  .g-vk {
    background: url('../assets/img/vk.png') no-repeat; }
  .g-fb {
    background: url('../assets/img/fb.png') no-repeat; }
  .g-tw {
    background: url('../assets/img/tw.png') no-repeat; }
  .g-ok {
    background: url('../assets/img/ok.png') no-repeat; }
  .g-social-icon {
    display: block;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    background-size: contain;
    margin: 0 15px;
    @include bp-table {
      width: 80px;
      height: 80px; }
    @include bp-table-600 {
      width: 15vw;
      height: 15vw;
      margin: 0 2vw; } } }
